.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.Teamsheet-logo {
  height: 7vmin;
  pointer-events: none;
  margin-top: 5px;
}


.Teamsheet-link {
  color: #ffffff;
}

section {
  /* border-bottom: 1px solid white;
  padding: 1rem; */
  height: 50vh;
  padding-top: 8vh;
  padding-bottom: 8vh;
  scroll-snap-align: start;
  background-color:#242424;
  color: white;
  text-align: center;
  /* text-align: center;
  position: relative; */
}

html {
  scroll-snap-type: y proximity;
}

body {
  scroll-snap-type: y proximity;
}

.Profile-big {
  height: 40vmin;
  border-radius: 40vmin;
}

.Button-test {
  cursor: pointer;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


